export default {
  props: { collections: Array },
  data() {
    return { selectedCollections: {} };
  },
  created() {
    this.collections.forEach((collection) => {
      this.$set(this.selectedCollections, collection._id, false);
    });
  },
  watch: {
    selectedCollections: {
      deep: true,
      handler(newVal) {
        this.$emit("input", newVal);
      },
    },
  },
};
